body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.header-menu{position:absolute;width:150px;top:90px;background-color:#161616}.header-menu a{color:#ffffff}

.nav-button-wrap{height:40px;width:40px;cursor:pointer;position:relative;top:25px}.nav-button-wrap-text{position:absolute;top:50%;transform:translate(0, -50%);font-weight:600;font-size:10px;text-transform:uppercase;right:-40px;letter-spacing:2px}.nav-button{position:absolute;width:20px;height:20px;top:50%;left:50%;margin-left:-11px;margin-top:-6px}.nav-button span{height:2px;background-color:#ffffff;margin:0 0 4px 0;float:left;transition:all 200ms linear}.nav-button-first{width:50%}.nav-button-mid{width:100%}.nav-button-last{width:100%}.nav-button:hover .nav-button-first{width:100%}.nav-button.active .nav-button-first{opacity:0}.nav-button.active .nav-button-mid{transform:rotate(-45deg);margin-bottom:0;margin-top:-1px}.nav-button.active .nav-button-last{transform:rotate(45deg);margin-bottom:0;margin-top:-2px;top:0px}

.header{height:90px;padding:0 60px;background-color:#161616;position:relative}.header-logo{position:absolute;left:50%;top:35px;transform:translate(-50%, 0)}.header-logo img{height:15px}

.Menu{--menu-size: 2rem;position:relative}.Menu__icon{display:inline-block;height:var(--menu-size);width:var(--menu-size)}.Menu__menu-toggle{line-height:var(--menu-size);border:none;padding:0;text-align:center}.Menu__navigation{background:black;color:white;position:absolute;top:var(--menu-size);left:0;display:none}.Menu__navigation--open{display:block}

.NewHeader{background:#000;color:white}.NewHeader--show-grid .NewHeader__wrapper{background:red}.NewHeader--show-grid .NewHeader__logo{background:#00aeff}.NewHeader__wrapper{max-width:60rem;padding:0 1rem;height:5rem;margin:auto;display:flex;justify-content:center;align-items:center}.NewHeader__logo{flex:1 1;text-align:center}.NewHeader__menu{position:relative}


